import React,{useState, useEffect} from 'react'
import axios from "axios";
function Movies() {
    const [search, setSearch] = useState("world")
    const [movies, setMovies] = useState([]);
    const [term, setTerm] = useState(search)
    const handleClick = (e)=>{
       setTerm(search)
       setSearch("")
    }
    useEffect(()=>{
      axios.get(`https://www.omdbapi.com/?s=${term}&apikey=b4563439`).then(res=>{
        //console.log(res.data.Search)
        setMovies(res.data.Search)
      })
    },[term])
  
    // show Details
    const showDetails = (id)=>{
      console.log(id)
     
      localStorage.setItem("movieId", JSON.stringify(id));
      window.location.href = "/movie-details";
    }
    return (
        <React.Fragment>
           <h1 className="text-center text-white p-2">Movies React App</h1>
            <div className="row m-1">  
                <div className="col-lg-8 row px-2 py-3 m-auto">
                <div className="col-xs-9 col-sm-10 col-md-10 col-lg-10 p-0">
                <input type="text"  value={search}  onChange={(e)=>setSearch(e.target.value)}  className="form-control" />
                </div>
                <div className="col-xs-3 col-sm-2 col-md-2 col-lg-2 p-0">
                <button className="button" onClick={handleClick} >Search</button>
                </div>
                </div>
            </div>
            <div className="row m-auto">
                <div className="col-12 row m-auto">
                {movies !== undefined ? movies.map(movie=>
                    <div className="col-md-4 col-lg-3 card" key={movie.imdbID}>
                    <div className="card-header">
                    <img src={movie.Poster} alt="pic" className="card-img-top" />
                    </div>
                    <hr/>
                    <div className="card-body">
                    <h5>{movie.Title}</h5>
                    <button className="button show-details" onClick={()=>showDetails(movie.imdbID)} >Details</button>
                    </div>
                    </div>
                    ) : <h1 className="m-auto text-white">No Result</h1> }
                </div>
            </div> 
        </React.Fragment>
    )
}

export default Movies
