import React,{useState, useEffect} from 'react'
import axios from "axios";
import {Link} from "react-router-dom";
function MovieDetails() {
   const [id, setId] = useState(JSON.parse(localStorage.getItem("movieId")))
   const [movie, setMovie] = useState({})
    useEffect(()=>{
     console.log(localStorage.getItem("movieId"))
     axios.get(`https://www.omdbapi.com/?i=${id}&apikey=b4563439`).then(res=>{
        console.log(res.data)
        setMovie(res.data)
      })
    },[id])
    return (
        <div className="row m-4">
            <div className="col-12 row m-auto">
                <div className="col-lg-4">
                    <img src={movie.Poster} alt="poster" className="card-img-top" />
                </div>
                <div className="col-lg-8 text-white m-0">
                    <table className="table text-white table-borderless p-0" >
                        <tbody>
                        <tr>
                            <th>Title</th>   
                            <th>{movie.Title}</th>
                        </tr>
                        <tr className="plot">
                            <th>Actors</th>
                            <td>{movie.Actors}</td>
                        </tr>
                        <tr>
                            <th>Awards</th>
                            <td>{movie.Awards}</td>
                        </tr>
                        <tr>
                            <th>Box Office</th>
                            <td>{movie.BoxOffice}</td>
                        </tr>
                        <tr>
                            <th>Coutry</th>
                            <td>{movie.Country}</td>
                        </tr>
                        <tr>
                            <th>Director</th>
                            <td>{movie.Director}</td>
                        </tr>
                        <tr>
                            <th>Genre</th>
                            <td>{movie.Genre}</td>
                        </tr>
                        <tr>
                            <th>Language</th>
                            <td>{movie.Language}</td>
                        </tr>
                        <tr>
                            <th>Released</th>
                            <td>{movie.Released}</td>
                        </tr>
                      
                        <tr className="plot">
                            <th>Plot</th>
                            <td><p>{movie.Plot}</p></td>
                        </tr>
                        <tr>
                            <th>Production</th>
                            <td>{movie.Production}</td>
                        </tr>
                        <tr className="plot">
                            <th>Writer</th>
                            <td>{movie.Writer}</td>
                        </tr> 
                        <tr>
                            <th>Imdb Rating</th>
                            <td>{movie.imdbRating}</td>
                        </tr>
                        <tr>
                            <th>
                                <Link to="/" className="button btn-home" >Go To Home</Link>
                            </th>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}

export default MovieDetails
